<template lang='pug'>
#app.container
  cf-nav-tabs(:tabs='navTabsItems'
    :active-index.sync='activeTabIndex')
  .tab-content
    .tab-pane(v-for='(item, index) in tabContentItems' :class='{ "active": index === activeTabIndex }') {{ item.text }}
</template>

<script>
import 'bootstrap/dist/css/bootstrap.min.css'

export default {
  name: 'app',
  data () {
    return {
      activeTabIndex: 0, // 当前 active 的tab索引
      navTabsItems: [ // nav-tabs配置
        { text: 'tab A' },
        { text: 'tab B' },
        { text: 'tab C' }
      ],
      tabContentItems: [ // content配置
        { text: 'content A' },
        { text: 'content B' },
        { text: 'content C' }
      ],
    }
  },
  watch: {
    activeTabIndex (val, oldVal) {
      console.log(`to: ${this.navTabsItems[val].text}, form: ${this.navTabsItems[oldVal].text}`)
    }
  }
}
</script>
